.contactBody {
    height: 100vh;
}

.container {
    flex-direction: column;
    justify-content: flex-start;
    border: 1px solid white;
}

.contacts {
    width: 100%;
    border: 1px solid white;
    display: flex;
}

.contact__aboutMe {
    flex: 0 0 33.33%;
}

.contactForm {
    flex: 0 0 66.66%;
    max-width: 66.66%;
}

.form {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}


.inputTags {
    flex: 0 1 33.33%;

}

input[type=text], input[type=text], textarea {
    border: 1px solid #111;
    background: #252525;
    color: #fff;
    padding: 11px 26px;
    margin-bottom: 30px;
    border-radius: 30px;
    outline: none !important;
    transition: .3s;
    box-sizing: border-box;
}

textarea {
    height: 160px;
    width: 100%
}

.textarea {
    margin: 0;
    flex: 0 0 100%;
}

input[type=text]:focus, input[type=text]:focus, textarea:focus {
    border: 1px solid #ffb400;
}

